import React from 'react'
import styles from './styles.sass'
import BottomBar from '../BottomBar'
import cn from 'classnames'

export interface IOrderRecapitulationProps {
	isActive: boolean
}

export interface IOrderRecapitulationActionProps {
	onClose: () => void
}

interface IState {
}

export default class OrderRecapitulation extends React.Component<
	IOrderRecapitulationProps & IOrderRecapitulationActionProps, IState
> {

	state: IState = {
	}

	render() {
		const { isActive, onClose } = this.props

		return (
			<section className={cn(styles.wrapper, isActive && styles.isActive)}>
				<button role="button" onClick={onClose}>Close</button>
				Recap
				<BottomBar isActive={true}>
					Continue <i className="fas fa-arrow-right"></i>
				</BottomBar>
			</section>
		)
	}
}



