import React from 'react'
import styles from './styles.sass'
import { IProduct } from '../../reducers/products'
import cn from 'classnames'
import formatVolumePrice from '../../utils/formatVolumePrice';
import formatIngredients from '../../utils/formatIngredients';

export interface IProductProps {
	product?: IProduct
}

export interface IProductActionProps {
	addToBasket?: (product: IProduct) => void
}

interface IState {
	imageLoaded: boolean
	waitingForImageLoad: boolean
}

export default class Product extends React.Component<
	IProductProps & IProductActionProps, IState
> {
	static placeholder = (
		<svg className={cn(styles.imageIn, styles.viewPlaceholder)} version="1.0" xmlns="http://www.w3.org/2000/svg"
		width="1024.000000pt" height="768.000000pt" viewBox="0 0 1024.000000 768.000000"
		preserveAspectRatio="xMidYMid meet">
			<g transform="translate(0.000000,768.000000) scale(0.100000,-0.100000)"
			fill="currentColor" stroke="none">
			<path d="M4574 7505 c-70 -15 -130 -43 -158 -74 -16 -18 -19 -35 -17 -114 1
			-50 6 -93 11 -95 5 -2 20 -12 34 -23 20 -16 26 -30 26 -60 0 -26 5 -42 15 -45
			8 -4 15 -14 15 -24 0 -11 5 -22 10 -25 14 -9 12 -93 -5 -225 -54 -412 -143
			-767 -303 -1205 -137 -377 -196 -594 -256 -946 -65 -374 -61 -241 -61 -2269 0
			-1763 1 -1848 18 -1905 59 -190 120 -235 360 -263 187 -21 1346 -24 1612 -4
			94 7 218 17 276 22 169 13 241 56 295 175 50 110 49 67 49 1970 0 1874 1 1830
			-46 2150 -58 397 -138 694 -313 1169 -144 389 -252 866 -281 1244 -5 69 -4 83
			9 88 9 3 16 14 16 24 0 11 5 22 10 25 6 3 10 22 10 42 0 26 8 42 30 63 29 27
			30 30 30 122 0 90 -1 95 -27 120 -79 73 -126 78 -761 77 -407 0 -552 -4 -598
			-14z"/>
			</g>
		</svg>
	)

	state: IState = {
		imageLoaded: false,
		waitingForImageLoad: true,
	}

	componentDidMount() {
		setTimeout(() => {
			this.setState({ waitingForImageLoad: false })
		}, 20)
	}

	render() {
		const { product, addToBasket } = this.props
		const { imageLoaded, waitingForImageLoad } = this.state

		const name = product ? product.name : ''
		const price = product ? formatVolumePrice(product.volume, product.price) : ''
		const ingredients = product ? formatIngredients(product.ingredients) : ''
		const color = product && product.color

		return (
			<section
				className={cn(styles.wrapper, !product && styles.viewShadow)}
				style={{ color }}
			>
				<div className={styles.image}>
					{product && (
						<button
							className={styles.addToCart}
							role="button"
							aria-label="Pidat do koku"
							onClick={() => addToBasket(product)}
						/>
					)}
					<div
						className={cn(
							styles.imageFrame,
							!waitingForImageLoad && !imageLoaded && styles.isPlaceholderVisible,
							imageLoaded && styles.isRealVisible,
						)}
					>
						{Product.placeholder}
						{product && (
							<img
								className={cn(styles.imageIn, styles.viewReal)}
								src={product.image}
								onLoad={this.onImageLoaded.bind(this)}
							/>
						)}
					</div>
				</div>
				<div className={styles.info}>
					<div className={styles.infoIn}>
						<h1 className={styles.name}>{name}</h1>
						<p className={styles.price}>{price}</p>
						<p className={styles.note}>{ingredients}</p>
					</div>
				</div>
			</section>
		)
	}

	private onImageLoaded() {
		this.setState({ imageLoaded: true })
	}
}



