import handleActions from '../utils/handleActions'
import { SET_USER, SetUserAction, UNSET_USER } from '../actions/user'

export interface IUserState {
	id: string
	email: string
	token: string
	loggedIn: boolean
}

const persistentState = localStorage.userState

const emptyState: IUserState = {
	id: '',
	email: '',
	token: '',
	loggedIn: false,
}
const initialState: IUserState = (persistentState && JSON.parse(persistentState)) || emptyState

// tslint:disable:function-name
export default handleActions(
	{
		[SET_USER](state, { payload }: SetUserAction) {
			const newState = {
				...state,
				id: payload.id,
				email: payload.id,
				token: payload.token,
				loggedIn: true,
			}
			localStorage.userState = JSON.stringify(newState)
			return newState
		},
		[UNSET_USER]() {
			localStorage.removeItem('userState')
			return emptyState
		},
	},
	initialState,
)



