import { connect } from 'react-redux'
import { IState } from '../reducers'
import BasketPreview, {
	IBasketPreviewActionProps,
} from '../components/BasketPreview'

const mapStateToProps = (state: IState, ownProps: IBasketPreviewActionProps) => ({ items: state.basket, ...ownProps })
const mapDispatchToProps = {}

export default connect(
	mapStateToProps,
	mapDispatchToProps,
)(BasketPreview)



